*{
  margin: 0;
  padding: 0;
  font-family: 'Pretendard-Regular';
    src: url('https://fastly.jsdelivr.net/gh/Project-Noonnu/noonfonts_2107@1.1/Pretendard-Regular.woff') format('woff');
    font-weight: 600;
    font-style: normal;
}

.study-content-container{
    align-items: center;
    justify-content: center;
    width: 4480px;
    margin: auto;
    position: relative;
    /* background-color: #61dafb; */
    display: flex;
    flex-wrap: nowrap;
    overflow: hidden;
    transition: transform 0.5s ease;
}

.study-button-item{
    width: 620px;
    height: 620px;
    box-sizing: border-box;
    padding: 20px;
    background-color: #f1f1f1;
    border: 1px solid #FFFFFF;
    border-radius: 40px;
    margin: 10px 10px;
    display: flex;
    flex-direction: column;
    align-items: center;
    box-shadow: 0px 5px 5px -2px rgba(0, 0, 0, 0.25);
    transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.study-button-item .intitle, .button-item .incomment, .button-item .incomment2 {
  transition: transform 0.3s ease;
}

.study-button-item:hover {
  transform: scale(1.05);
  box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.2);
}

.study-button-item:hover .intitle,
.study-button-item:hover .incomment,
.study-button-item:hover .incomment2 {
  transform: scale(1.05);
}

.study-top-section{
    background-color: #676767;
    width: 100%;
    height: 45px;
    margin-bottom: 10px;
}

.study-mid-section{
    /* background-color: cornflowerblue; */
    width: 100%;
    height: 350px;
    margin-bottom: 10px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

#study-mid-main-comment{
  font-size: 50px;
  font-weight: 500;
}

#study-mid-sub-comment{
  font-size: 40px;
  font-weight: 500;
  color: rgb(0, 0, 0, 0.3);
}

.study-intitle{
  font-size: 25px;
  font-weight: 500;
  margin: 270px 0 0 0;
}

.study-incomment{
  font-size: 20px;
  font-weight: 500;
}

.study-incomment2{
  font-size: 20px;
  font-weight: 500;
}

.study-inpng{
  position: absolute;
  width: 380px;
  height: 270px;
  border-radius: 30px;
  margin: -10px 0 0 0;
  justify-content: flex-start;
}

.study-slider-button-container{
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  margin: auto;
  position: absolute;
  background-color: #676767;
  top: 735px;
}

.study-slider-button{
  background: #333;
  color: white;
  border: none;
  padding: 10px;
  cursor: pointer;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  border-radius: 50%;
  width: 30px;
  height: 30px;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 10;
}

.study-slider-button.left{
  left: 20px;
}

.study-slider-button.right{
  right: 20px;
}

.popup-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}

.popup-content {
  background: white;
  padding: 20px 30px;
  border-radius: 40px;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.3);
  width: 1300px;
  height: 800px;
  max-width: 100%;
  text-align: left;
  z-index: 1001;
}


.popup-content button {
  background-color: #333;
  color: white;
  border: none;
  padding: 10px 20px;
  border-radius: 5px;
  cursor: pointer;
  margin-top: 20px;
}

.popup-content button:hover {
  background-color: #555;
}

.pop-main{
  font-size: 40px;
  margin: 0;
}

.pop-sub{
  font-size: 20px;
  margin: 0;
}

.popup-header {
  display: flex;
  justify-content: space-between; /* 제목과 화살표를 양 끝에 배치 */
  align-items: center; /* 세로 중앙 정렬 */
}

.close-arrow {
  font-size: 40px; /* 화살표 크기 */
  font-weight: bold;
  cursor: pointer;
  color: #333;
  transition: color 0.3s ease;
}

.close-arrow:hover {
  color: #555; /* 호버 시 색 변경 */
}

.popup-keyword-button {
  border: 1px solid black; /* 테두리는 항상 검은색 */
  background-color: black; /* 기본 배경 검정 */
  color: white; /* 기본 글씨 흰색 */
  padding: 5px 10px;
  border-radius: 20px;
  cursor: pointer;
  transition: all 0.3s ease;
  margin: 10px;
  font-size: 20px;
  text-align: center;
}

.popup-keyword-button.selected {
  background-color: white; /* 클릭 후 배경 흰색 */
  color: black; /* 클릭 후 글씨 검정 */
}

.popup-keyword-links {
  margin-top: 20px;
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.keyword-link-item a {
  color: #007bff;
  text-decoration: none;
  font-size: 18px;
  /* 긴 링크 줄바꿈 관련 스타일 */
  word-wrap: break-word; /* 긴 단어를 줄바꿈 */
  word-break: break-all; /* 긴 링크가 영역을 넘어서면 줄바꿈 */
  overflow-wrap: anywhere; /* 브라우저가 자동으로 적당한 곳에서 줄바꿈 */
}

.keyword-link-item a:hover {
  text-decoration: underline;
}

.keyword-link-item {
  max-width: 100%; /* 부모 컨테이너의 너비 제한 */
  overflow: hidden; /* 영역 넘침 방지 */
}