*{
  margin: 0;
  padding: 0;
  font-family: 'Pretendard-Regular';
    src: url('https://fastly.jsdelivr.net/gh/Project-Noonnu/noonfonts_2107@1.1/Pretendard-Regular.woff') format('woff');
    font-weight: 600;
    font-style: normal;
}

.content-container{
    align-items: center;
    justify-content: center;
    width: 100%;
    margin: auto;
    position: relative;
    /* background-color: #61dafb; */
    display: flex;
    flex-wrap: wrap;
}

.button-item{
    width: 620px;
    height: 620px;
    box-sizing: border-box;
    padding: 20px;
    background-color: #f1f1f1;
    border: 1px solid #FFFFFF;
    border-radius: 40px;
    margin: 10px 10px;
    display: flex;
    flex-direction: column;
    align-items: center;
    box-shadow: 0px 5px 5px -2px rgba(0, 0, 0, 0.25);
    transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.button-item .intitle, .button-item .incomment, .button-item .incomment2 {
  transition: transform 0.3s ease;
}

.button-item:hover {
  transform: scale(1.05);
  box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.2);
}

.button-item:hover .intitle,
.button-item:hover .incomment,
.button-item:hover .incomment2 {
  transform: scale(1.05);
}

.top-section{
    background-color: #676767;
    width: 100%;
    height: 45px;
    margin-bottom: 10px;
}

.mid-section{
    /* background-color: cornflowerblue; */
    width: 100%;
    height: 350px;
    margin-bottom: 10px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

#mid-main-comment{
  font-size: 50px;
  font-weight: 500;
}

#mid-sub-comment{
  font-size: 40px;
  font-weight: 500;
  color: rgb(0, 0, 0, 0.3);
}

.intitle{
  font-size: 30px;
  font-weight: 600;
  margin: 440px 0 0 0;
}

.incomment{
  font-size: 20px;
  font-weight: 500;
  margin-top: 40px;
}

.incomment2{
  font-size: 20px;
  font-weight: 500;
}

.inpng{
  position: absolute;
  width: 600px;
  height: 400px;
  border-radius: 30px;
  margin: -10px 0 0 0;
  justify-content: flex-start;
}