@import url('https://fonts.googleapis.com/css2?family=IBM+Plex+Sans+KR:wght@500&display=swap');

*{
  margin: 0;
  padding: 0;
}

.travel-content-container{
  align-items: center;
  justify-content: center;
  width: 4480px;
  margin: auto;
  position: relative;
  /* background-color: #61dafb; */
  display: flex;
  flex-wrap: nowrap;
  overflow: hidden;
  transition: transform 0.5s ease;
}

.travel-button-item{
  width: 620px;
  height: 620px;
  box-sizing: border-box;
  padding: 20px;
  background-color: #f1f1f1;
  border: 1px solid #FFFFFF;
  border-radius: 40px;
  margin: 10px 10px;
  display: flex;
  flex-direction: column;
  align-items: center;
  box-shadow: 0px 5px 5px -2px rgba(0, 0, 0, 0.25);
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.travel-button-item .travel-intitle, .travel-button-item .travel-incomment, .travel-button-item .travel-incomment2 {
  transition: transform 0.3s ease;
}

.travel-button-item:hover {
  transform: scale(1.05);
  box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.2);
}

.travel-button-item:hover .intitle,
.travel-button-item:hover .incomment,
.travel-button-item:hover .incomment2 {
  transform: scale(1.05);
}

.travel-top-section{
    background-color: #676767;
    width: 100%;
    height: 45px;
    margin-bottom: 10px;
}

.travel-mid-section{
    /* background-color: cornflowerblue; */
    width: 100%;
    height: 350px;
    margin-bottom: 10px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.travel-text-skin{
  font-family: "IBM Plex Sans KR", sans-serif;
    font-style: normal;
}

#travel-mid-main-comment{
  font-size: 50px;
  font-weight: 500;
}

#travel-mid-sub-comment{
  font-size: 40px;
  font-weight: 500;
  color: rgb(0, 0, 0, 0.3);
}

.travel-intitle{
  font-size: 25px;
  font-weight: 500;
  margin: 270px 0 0 0;
}

.travel-incomment{
  font-size: 20px;
  font-weight: 500;
}

.travel-incomment2{
  font-size: 20px;
  font-weight: 500;
}

.travel-inpng{
  position: absolute;
  width: 380px;
  height: 270px;
  border-radius: 30px;
  margin: -10px 0 0 0;
  justify-content: flex-start;
}

.travel-slider-button-container{
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  margin: auto;
  position: absolute;
  background-color: #676767;
  top: 735px;
}

.travel-slider-button{
  background: #333;
  color: white;
  border: none;
  padding: 10px;
  cursor: pointer;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  border-radius: 50%;
  width: 30px;
  height: 30px;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 10;
}

.travel-slider-button.left{
  left: 20px;
}

.travel-slider-button.right{
  right: 20px;
}

.popup-content {
  width: 100%; /* 부모의 크기에 맞춰 가로폭을 설정 */
  max-width: 1260px; /* 최대 너비를 1260px로 제한 */
  margin: 0 auto; /* 중앙 정렬 */
  overflow-y: auto; /* 컨텐츠가 넘칠 경우 스크롤 추가 */
  overflow-x: hidden;
}